              </v-ons-toolbar-button>
          </div>
      </v-ons-toolbar>
        
        <div class="cardSpace-wrapper">
        <!--以下、Muuriグリッド-->
        <div class="grid" v-show="gridIf">
          <div class="item" v-for="(item, k) in get.cards" v-bind:key="k">
            <div class="item-content" v-bind:id="k">
              <router-link :to="{path:'Cards/:' + k}" v-show='!renameShow'>
                <div class="card" v-on:click="viewLe">
                  {{ item.name | txtFil}}
                </div>
              </router-link>
                <div class="card cardRename" v-show='renameShow'>
                  <input type="text" class="renameTxt" v-bind:class="k" v-on:blur="renameBlur(k)" v-bind:value='item.name' />
                </div>
                <div class="cardBtns">
                  <!-- <transition name="btnShow" mode="out-in"> -->
                    <div class="editCl" v-show='!delShow'>
                      <router-link :to="{path:'Cards/:' + k}">
                        <button class="editBtn" v-on:click="viewSt">一覧</button>
                      </router-link>
                      <router-link :to="{path:'Cards/:' + k}" v-show='!delShow'>
                        <button class="editBtn" v-on:click="viewTe">テスト</button>
                      </router-link>
                    </div>
                  <!-- </transition> -->
                  <!-- <transition name="btnShow" mode="out-in"> -->
                    <button class="delBtn" v-on:click="delCard(item, k)" v-bind:id="'del' + k" v-show='delShow'>削除</button>
                  <!-- </transition> -->
                </div>
            </div>
          </div>
        </div><!--grid-->
        </div><!--cardSpace-->
      <!-- <Footer/> -->
      <v-ons-dialog
      cancelable
      :visible.sync="newDialog"
      >
        <p class="dialogContent">新規作成</p>
        <div class="dialogContent">
          <v-ons-input type="text" class="newCardInp" placeholder="新しい単語帳の名前" v-on:keyup.enter="createCard" v-model="newCardsName" />
        </div>
        <div class="dialogContent">
          <v-ons-button modifier="quiet" v-on:click="createCard">OK</v-ons-button>
          <v-ons-button modifier="quiet" v-on:click="dialogCloseMe">CANCEL</v-ons-button>
        </div>
      </v-ons-dialog>

      <div class="tabbar">
        <label class="tabbar__item">
            <input type="radio" name="tabbar-c" class="radio" v-on:click="dialogShowMe">
            <button class="tabbar__button">
            <div class="tabbar__label">新規作成</div>
            </button>
        </label>

        <label class="tabbar__item">
            <input type="radio" name="tabbar-c" class="radio" v-on:click="rename">
            <button class="tabbar__button">
            <div class="tabbar__label">名前変更</div>
            </button>
        </label>

        <label class="tabbar__item">
            <input type="radio" name="tabbar-c" class="radio" v-on:click="delBtnShow">
            <button class="tabbar__button">
            <div class="tabbar__label">削除</div>
            </button>
        </label>
      </div>
    </div>
  </div><!--cardsBG-->
</template>

